// src/components/ThreeBackground/index.tsx
"use client";
import { Canvas } from "@react-three/fiber";
import { OrbitControls, Environment } from "@react-three/drei";

export default function ThreeBackground() {
  return (
    <Canvas style={{ position: "fixed", inset: 0, zIndex: -1 }}>
      <ambientLight intensity={0.5} />
      <Environment preset="starfield" />  {/* Predefined skybox */}
      <mesh rotation={[0, 0, Math.PI / 4]}>
        <torusGeometry args={[1, 0.25, 16, 32]} />
        <meshBasicMaterial color="#ff7e5f" wireframe />
      </mesh>
      <OrbitControls autoRotate autoRotateSpeed={2} enableZoom={false} />
    </Canvas>
  );
}